import React, { Suspense, useEffect, useRef, lazy } from "react";
import { isTagbox } from "../../../../actions/api";
import Media from "./Media";
import InViewWrapper from "../../Elements/InViewWrapper";
const Network = lazy(() => import("../../Elements/Network/Network"));
const ShopButton = lazy(() => import("../../Elements/ShopButton/ShopButton"));
const CTAButton = lazy(() => import("../../Elements/CTA/CtaButton"));

const Card = ({ Post, CardStyle, index, ThemeInfo, onClickToCTA, ThemeStyle, ProductSetting, onClickPopUpSlider }) => {
  const isMerge = ThemeInfo.id == 81 ? false : true;
  const overlay = isTagbox ? "tb_ht_color_overlay" : "tb_ht_post_overlay-dark";
  const overlayStyle = { backgroundColor: isTagbox ? CardStyle.style.background : "" };
  const cardRadius = isMerge && isTagbox ? "50%" : null;

  useEffect(() => {
    if (Post.isPopUp) onClickPopUpSlider(index, Post);
  }, [index, Post.isPopUp, Post]);
  return <InViewWrapper>
    {({ ref, inView }) => (
      <div id={`tb_ht_id-${Post.id}`} className="tb_ht_post_wrapper" style={{ padding: CardStyle.style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} tb-network={Post.network.id} tb-product={Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} area-label={Post.content.text} role="article" ref={ref} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId}>
        <div className="tb_ht_post_in" style={{ borderRadius: cardRadius }} role="button" aria-label="Popup" tabIndex={"0"}>
          <div className="tb_ht_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
            <Media key={index} Post={Post} CardStyle={CardStyle} ProductSetting={ProductSetting}  ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} inView={inView} />
            <div className="tb_ht_post_details">
              <div className="tb_ht_social_">
                <Suspense fallback={<></>}><Network networkClass={"tb_ht_social_ico_"} networkId={Post.networkId} iconStyle={CardStyle.icon} /></Suspense>
              </div>
              {CardStyle.shopText != "" && Post.isProduct ? (
                <Suspense fallback={<></>}><ShopButton CardStyle={CardStyle} mode={1} btnClass={"tb_ht_btn_wrap"} /></Suspense>
              ) : null}
              {Post.ctaStatus ? (
                <div className="tb_ht_post_cta" onClick={(e) => { e.stopPropagation(); }}>
                  <Suspense fallback={<></>}><CTAButton ctaClass={"tb_ht_post_cta_btn"} Post={Post} onClickToCTA={onClickToCTA} /></Suspense>
                </div>
              ) : null}
            </div>
            <div className={`tb_ht_post_overlay ${overlay}`} style={overlayStyle}>
              <div></div>
            </div>
          </div>
        </div>
      </div>
    )}
  </InViewWrapper>
};
export default Card;
